}

.cl-btn-6-in {
  width: inherit;
  text-align: center;
}

.cl-btn-6-txt {
  text-transform: uppercase;
  color: #000;
  opacity: 0;
  cursor: pointer;
  font-family: Verdana;
  font-size: 10px;
  font-weight: 700;
  line-height: 40px;
  transition: all .3s ease-in;
}

.cl-btn-6-in:before, .cl-btn-6-in:after {
  content: "";
  height: 4px;
  width: inherit;
  background: #000;
  transition: all .3s ease-in;
  position: absolute;
  left: 0;
}

.cl-btn-6-in:before {
  top: calc(50% - 4px);
  transform: rotate(45deg);
}

.cl-btn-6-in:after {
  bottom: 50%;
  transform: rotate(-45deg);
}

.close-btn:hover .cl-btn-6-txt {
  opacity: 1;
}

.close-btn:hover .cl-btn-6-in:before, .close-btn:hover .cl-btn-6-in:after {
  transform: rotate(0);
}

.close-btn:hover .cl-btn-6-in:before {
  top: 0;
}

.close-btn:hover .cl-btn-6-in:after {
